<template>
    <div class="editRentaCar w-1/4 fixed-center">
        <div class="app bg-dark-1 slide-in-bck-bottom">
            <q-bar>
                <div class="cursor-pointer font-light font-primary" @click="saveVehicles">Kaydet</div>
                <div class="cursor-pointer font-light font-primary">Kiralk Noktay Sil</div>
                <q-space />
                <q-btn dense flat icon="close" @click="this.$parent.dynamicPage.dynamicPageSelection = -1"/>
            </q-bar>
            <div class="inner bg-dark-2">
                <div class="text font-normal font-primary" v-if="position">uan dzenlenen Pozisyon:<br> {{'x: ' + position.x + " | y: " + position.y + " | z: " + position.z}}</div>
                <div class="add q-pa-md row inline full-width">
                    <q-input label="Ara Ad(rn: adder)" class="col-8" v-model="addVehicleName" filled stack-label dark @keypress.enter="addVehicle"/>
                    <q-space />
                    <q-btn push label="Ekle" class="col-3" color="positive" size="md" text-color="primary" @click="addVehicle"/>
                </div>
                <hr>
                <div class="text font-primary font-normal q-px-md">Ara Listesi</div>
                <div class="vehicleListBox max-h-80 overflow-auto">
                    <div class="vehicleList font-primary font-normal q-pa-sm " v-for="veh in vehicles" :key="veh">
                        <div class="q-px-md q-py-xxs bg-dark-1 full-width row inline border border-gray-600">
                            <div class="text q-pt-md col-3">{{veh.name}}</div>
                            <div class="text q-pt-md col-3"><span style="color:green;">$</span>{{veh.price}}</div>
                            <q-input type="number" class="col-4" v-model="veh.price"  label="Ara Fiyat"  filled stack-label dark />
                            <q-btn dense flat icon="close" @click="removeVehicle(veh)" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
        
    </div>
</template>
<script>
export default {
    data: () => ({
        Id: undefined,
        position: undefined,
        dimension: undefined,
        vehicles: [],
        // ----------
        addVehicleName: ''
    }),
    mounted() {
        if('alt' in window) 
        {
            alt.emit('Plugin:RentACar:AdminEdit');
            alt.on('Plugin:RentACar:AdminEdit', this.placeEditData)
        }
    },
    
    methods: {
        placeEditData(Id, position, dimension, vehicles) {
            this.Id = Id;
            this.position = position;
            this.dimension = dimension;
            if(vehicles){                
                this.vehicles = vehicles;

            }
        },

        addVehicle() {
            if(this.addVehicleName.length <= 1){
                return;
            }
            else{
                this.vehicles.push({
                    name: this.addVehicleName.toLowerCase(),
                    price: 100
                });
                this.addVehicleName = '';
            }
        },
        removeVehicle(veh) {
            this.vehicles.splice(this.vehicles.indexOf(veh), 1)
        },

        saveVehicles() {
            alt.emit('Plugin:RentACar:AdminSave', this.Id, this.vehicles);
            this.$parent.dynamicPage.dynamicPageSelection = -1;
        }
    }
}
</script>