<template>
    <div :class="(maximize) ? 'editBusiness fixed-center font-primary font-normal bg-dark-1 w-1/2 rounded-lg':'editBusiness font-primary font-normal bg-dark-1 fullscreen w-screen h-screen'">
        <div class="header">
            <q-bar style="min-width: 450px;" class="bg-dark-1 text-white rounded-borders">
                <div class="text">yeri Dzenleme</div>
                <q-space />
                <q-btn dense flat :icon="(maximize) ? 'fullscreen':'fullscreen_exit'" @click="maximize = !maximize" />
                <q-btn dense flat icon="close" @click="$parent.pages.editBusiness = false" />
            </q-bar>
        </div>
        
        <div class="inner q-pa-sm full-width">
            <q-img :src="biz.image" height="200px" fit="fill">
            <div class="text-h4 text-center font-bold font-accent bg-transparent absolute-full flex flex-center font-shadow">{{biz.name}}</div>
            </q-img>

            <div class="row inline full-width q-mt-xl q-pa-md">
                <div class="col-4">

                    <div class="text-h4">yeri bilgileri</div>
                    <hr>
                    <div class="q-mt-md">
                        <div class="name text-center">
                            <div class="name-text">
                                <div class="text-h6" v-if="settings.name">sim: {{biz.name}} [{{biz.Id}}]</div>
                                <q-input label="yeri Ad" v-model="biz.name" filled stack-label dark v-else/>
                                <div class="text-caption" @click="settings.name = !settings.name">(Dzenlemek iin tklayn)</div>
                            </div>
                        </div>

                        <div class="w-full">
                            <q-toggle v-model="biz.lock" color="green" :label="(biz.lock) ? 'Kilit: Ak':'Kilit: Kilitli'" class="font-light font-primary" />
                        </div>

                        <div class="image">
                            <q-input label="Resim" v-model="biz.image" filled stack-label dark/>
                        </div>

                        <div class="price q-pt-md">
                            <div class="text">Fiyat: ${{biz.price}}</div>
                            <div class="text">Giri creti: ${{biz.entrancePrice}}</div>
                            <div class="text">Kasa: ${{biz.vault}}</div>
                        </div>

                        <div class="owner text-right underline">
                            Sahip: {{biz.ownerName}} ({{biz.owner}})
                        </div>

                        <div class="tax underline q-pt-lg">
                            <div class="text">Vergi: ${{biz.defaultTax}}/saat</div>
                            <div class="text">Toplam Vergi Borcu: {{biz.totalTax}}</div>
                        </div>

                        <div class="security">
                            Gvenlik Seviyesi: {{biz.securityLevel}}/6
                        </div>

                        <div class="type text-h6 q-pt-xl">
                            Tip: {{getType(biz.type)}}
                        </div>
                    </div>

                </div>

                <div class="col-8 rightSide q-pl-md">
                    <q-tabs
                        v-model="tab"
                        inline-label
                        :breakpoint="0"
                        align="justify"
                        class="bg-dark-2 shadow-2 rounded-lg"
                    >
                        <q-tab name="interior" label="Interior Bilgileri" />
                        <q-tab name="staff" label="alanlar" />
                        <q-tab name="staffPermissions" label="Rtbeler" />
                    </q-tabs>

                    <q-tab-panels v-model="tab" animated>
                        <q-tab-panel name="interior" class="bg-dark-1 rightInner">
                            <div class="rightPanel text-right">
                                <div class="text-h6">Interior Bilgileri</div>
                                <div class="text">Interior Pozisyon:</div>
                                <div class="text">X: {{biz.interiorPosition.x}} | Y: {{biz.interiorPosition.y}} | Z: {{biz.interiorPosition.z}}</div>
                                <div class="text">Dimension: {{biz.interiorDimension}}</div>
                            </div>
                            <div class="edit">
                                <div class="text">Interior Prop:</div>
                                <div class="row inline full-witdh">
                                    <q-input v-model="biz.interiorSettings.prop" class="col-4" label="Prop" stack-label dark/>
                                    <q-space></q-space>
                                    <q-input v-model="settings.addProp" class="col-4" label="Entity" stack-label dark @keypress.enter="addProp"/>
                                </div>

                                <div class="text q-pt-lg">Entity Set:</div>
                                <div class="text" v-for="(prop,i) in biz.interiorSettings.entitySets" :key="i">
                                    <q-icon name="close" @click="removeProp(i)" /> Entity[{{i}}]: {{prop}}
                                </div>
                            </div>
                        </q-tab-panel>

                        <q-tab-panel name="staff" class="bg-dark-1 rightInner">
                            <q-list bordered :class="(maximize) ? 'max-h-96 rightInner overflow-auto rounded-borders': 'max-h-96 rightInner overflow-auto rounded-borders'">
                                <q-item-label header class="text-white">alan Listesi</q-item-label>

                                <q-item v-for="(staff) in staffList" :key="staff">
                                    <q-item-section avatar>
                                    <q-avatar>
                                        <img :src="staff.avatar">
                                    </q-avatar>
                                    </q-item-section>

                                    <q-item-section>
                                    <q-item-label>{{staff.name}} [{{getRankName(staff.Id)}}]</q-item-label>
                                    </q-item-section>

                                    <q-item-section side top>
                                       <q-select
                                            label="Rtbe"
                                            transition-show="scale"
                                            transition-hide="scale"
                                            filled
                                            v-model="staff.rank"
                                            :options="biz.ranks"
                                            option-value="Id"
                                            option-label="name"
                                            dark
                                            style="min-width: 250px;"
                                        />
                                    </q-item-section>
                                </q-item>

                                <!--<q-separator inset="item" />-->

                                
                            </q-list>
                            
                        </q-tab-panel>

                        <q-tab-panel name="staffPermissions" class="bg-dark-1">
                            <div class="text-h6">alan Rtbeleri</div>
                            <div class="create">
                                <div class="text">Yeni Rtbe</div>
                                <div class="row inline full-width q-py-md">
                                    <q-input v-model="settings.newRank" label="Rtbe Ad" class="w-1/2" filled stack-label dark @keypress.enter="addRank"/>
                                    <q-space />
                                    <q-btn label="Olutur" color="green" class="w-1/4" size="sm" @click="addRank"/>
                                </div>

                                <hr>
                            </div>
                            <div :class="(maximize) ? 'max-h-72 rightInner overflow-auto': 'max-h-96 rightInner overflow-auto'">
                                <div class="rank q-py-sm" v-for="rank in biz.ranks" :key="rank">
                                    <div class="text">
                                        Rtbe: {{rank.name}} [{{rank.Id}}] <q-icon name="close" @click="removeRank(rank.Id)" />
                                    </div>
                                    <div class="perms row inline">                                    
                                        <q-toggle v-model="rank.permission.lock" color="green" label="Kilit" class="font-light font-primary" />
                                        <q-toggle v-model="rank.permission.invite" color="green" label="Davet" class="font-light font-primary" />
                                        <q-toggle v-model="rank.permission.kick" color="green" label="ten karma" class="font-light font-primary" />
                                        <q-toggle v-model="rank.permission.vault" color="green" label="Kasa" class="font-light font-primary" />
                                    </div>
                                </div>
                            </div>

                            
                        </q-tab-panel>
                    </q-tab-panels>
                </div>

            </div>
        </div>

        <div class="button w-full row inline">
            <div class="col-6" />
            <q-space />
            <q-btn label="kaydet" color="green" class="col-2 q-mx-md q-my-sm" />
        </div>
    </div>
</template>

<script>
export default {
    data: () => ({
        maximize: true,
        tab: 'interior',
        settings: {
            name: false,
            addProp: '',
            newRank: '',
        },
        biz: {
            Id: 1,
            lock: false,
            image: 'https://media.discordapp.net/attachments/769644782472790056/903353742726926417/afishalloween_1.png?width=868&height=450',
            name: 'TH NightClub',
            price: 2000,
            entrancePrice: 20,
            owner: 1,
            ownerName: 'Cone Anderson',
            vault: 20,

            position: {x: 1, y: 1, z: 1},
            entranceDimension: 0,

            interiorPosition: {x: 999, y: 999, z: 999},
            interiorDimension: 1,

            interiorSettings: {
                prop: 'a_B_evProp',
                entitySets: ['a_b_pro', 'a_C_lights'],
                interiorTime: 11
            },

            defaultTax: 5,
            totalTax: 2000,
            securityLevel: 1,

            rankIdCounter: 3, // TODO rank counteri emitle.
            ranks: [
                {Id: 0, name: 'Garson', permission: {lock: false, invite: false, kick: false, vault: false}},
                {Id: 1, name: 'ef', permission: {lock: true, invite: true, kick: true, vault: true}},
                {Id: 2, name: 'CO-BOSS', permission: {lock: true, invite: true, kick: true, vault: true}}
            ],

            type: 1            
        },

        staffList: [
            {Id: 1, name: 'Cone Anderson', rank: 2, avatar: 'https://cdn.quasar.dev/img/avatar2.jpg'},
            {Id: 2, name: 'Steve Clawson', rank: 1, avatar: 'https://cdn.quasar.dev/img/avatar1.jpg'},
            {Id: 3, name: 'Senkron Test', rank: 2, avatar: 'https://cdn.quasar.dev/img/avatar2.jpg'},
            {Id: 4, name: 'Qua Test', rank: 1, avatar: 'https://cdn.quasar.dev/img/avatar1.jpg'},
            {Id: 5, name: 'Dracarys Test', rank: 2, avatar: 'https://cdn.quasar.dev/img/avatar2.jpg'},
            {Id: 6, name: 'Praxis TEst', rank: 1, avatar: 'https://cdn.quasar.dev/img/avatar1.jpg'},
            {Id: 7, name: 'Orji TEst', rank: 2, avatar: 'https://cdn.quasar.dev/img/avatar2.jpg'},
            {Id: 8, name: 'Farkitto', rank: 1, avatar: 'https://cdn.quasar.dev/img/avatar1.jpg'},
            {Id: 9, name: 'Nearble', rank: 2, avatar: 'https://cdn.quasar.dev/img/avatar2.jpg'},
            {Id: 10, name: 'Khueljin', rank: 1, avatar: 'https://cdn.quasar.dev/img/avatar1.jpg'},
            {Id: 11, name: 'QuantumZ', rank: 2, avatar: 'https://cdn.quasar.dev/img/avatar2.jpg'},
            {Id: 12, name: 'PYRZ', rank: 1, avatar: 'https://cdn.quasar.dev/img/avatar1.jpg'},
            {Id: 13, name: 'eddard', rank: 2, avatar: 'https://cdn.quasar.dev/img/avatar2.jpg'},
            {Id: 14, name: 'Sanform', rank: 1, avatar: 'https://cdn.quasar.dev/img/avatar1.jpg'}
        ]
    }),

    mounted() {
        
    },

    methods: {
        getType(type) {
            switch(type){
                case 0: return 'Normal';
                case 1: return 'Market';
                case 2: return 'Restourant';
                case 3: return 'itflik';
                case 4: return 'Bar';
                case 5: return 'GYM';
                case 6: return 'Berber';
                case 7: return 'Kyafeti';
                case 8: return 'Ara Kiralam';
                case 9: return 'Modifiye Dkkan';
                case 10: return 'Dvmeci';
                case 11: return 'Gazino';
                case 12: return 'Haber Kanal';
                case 13: return 'Klinik';
                case 14: return 'Otel';
                case 15: return 'Depo';
            }
        },
        addProp() {
            if(this.settings.addProp.length > 3) {
                this.biz.interiorSettings.entitySets.push(this.settings.addProp);
                this.settings.addProp = '';
            }
        },
        removeProp(id) {
            this.biz.interiorSettings.entitySets.splice(id, 1);
        },

        addRank() {
            if(this.settings.newRank.length <= 2)
                return;
            this.biz.ranks.push({
                Id: this.biz.rankIdCounter,
                name: this.settings.newRank,
                permission:{
                    lock: false,
                    invite: false,
                    kick: false,
                    vault: false,
                }
            });

            ++this.biz.rankIdCounter;
            this.settings.newRank = '';
        },

        removeRank(id) {
            let check = this.biz.ranks.find(x => x.Id == id);
            if(check) {
                this.biz.ranks.splice(this.biz.ranks.indexOf(check), 1);
            }
        },

        getRankName(Id) {
            let check = this.biz.ranks.find(x => x.Id == Id);
            if(check)
                return check.name;
            else return 'YOK'
        }
    }
}
</script>

<style lang="scss" scoped>
.rightInner::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #988C97;
}::-webkit-scrollbar
{
	width: 10px;
	background-color: #988C97;
}::-webkit-scrollbar-thumb
{
	background-color: #4B4E67;
	border: 2px solid #4B4E67;
}
</style>