<template>
    <div class="rentaCar fixed-center w-1/2 h-1/2">
        <div class="page bg-dark-1 w-full h-full font-normal font-primary">
            <q-bar>
                <q-space />
                <q-btn dense flat icon="close" @click="this.$parent.dynamicPage.dynamicPageSelection = -1"/>
            </q-bar>
            <div class="inner q-pa-sm">
                <div class="text-center">Ara kiralama ({{data.Id}}) servisine ho geldiniz.</div>
                <div class="text q-px-md">Menden ara seerek, belirlediiniz arac kiralayabilir ve kullanabilirsiniz.</div>
                <hr>
                <div class="text">Kiralamaya uygun aralar</div>
                <div class="row inline w-full h-full">
                    <q-card class="rentcar-card bg-dark-2 font-primary font-normal col-3 q-ma-md" v-for="veh in data.veh" :key="veh">
                        <q-img :src="getImage(veh.name)">
                            <div class="absolute-bottom">
                            <div class="text-h6 font-light">{{veh.name}}</div>
                            <div class="text-subtitle2"><span style="color: green;">$</span>{{veh.price}}</div>
                            </div>
                        </q-img>
                        

                        <q-card-actions class="bg-accent">
                            <q-btn flat @click="rentCar(veh.name)">Hemen Kirala</q-btn>                            
                        </q-card-actions>
                    </q-card>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
export default {
    props: {
        data: Object,
    },

    methods: {
        getImage(name) {
            return require('../../../assets/vehicle_images/' + name + '.png')
            //return '/assets/vehicle_images/' + name + '.png'; zentorno
        },
        rentCar(name) {
            alt.emit('Plugin:RentACar:RentCar', this.data.Id, name);
        }
    },

    
}
</script>