<template>
    <div class="location fixed-bottom-left q-px-lg q-py-md w-1/6 row inline justify-evenly">
        <div class="text-center font-normal font-primary col-5 font-shadow">{{location}}</div>
        <div class="text-h4 font-normal font-primary col-4 text-center font-shadow">| {{compas}} |</div>
        <q-icon :name="inCar ? 'directions_car':'transfer_within_a_station'" color="primary" size="32px" class="col-2" />
    </div>
</template>

<script>
export default {
    data: () => ({
        inCar: false,
        location: '',
        compas: ''
    }),

    mounted() {
        if('alt' in window) {
            alt.on('Gui:Street:SetLocation', this.setLocation)
            alt.on('Gui:Street:SetCompas', this.setCompass)
            alt.emit('Gui:Street:Int')
        }
    },

    methods: {
        setLocation(name){
            this.location = name
        },
        setCompass(comp) {
            this.compas = comp;
        }
    }
}
</script>