<template>
    <div class="money fixed-right q-pa-md h-24 font-shadow" v-if="this.$store.state.character">
        <div class="text-h6 text-white font-normal">
            <q-icon name="attach_money" color="green" size="24px" />
            {{ this.$store.state.character.cash }}
        </div>

        <div class="text font-normal font-primary">{{ characterName.replace('_', ' ') + ' (ID: ' + this.$store.state.character.Id + ')' }}</div>

        <div class="text-right font-normal font-primary"> {{totalPlayer}}<q-icon name="person" size="16px" /></div>
    </div>
</template>

<script>
export default {
    mounted() {
        if('alt' in window){
            alt.on('Gui:TotalPlayer:Set', this.setTotalPlayer)
            alt.emit('Gui:TotalPlayer:Int')
        }
    },

    data: () => ({
        totalPlayer: 0
    }),

    methods: {
        setTotalPlayer(total) {
            this.totalPlayer = total;
        }
    },

    computed: {
        'characterName': function() {
            //return JSON.parse(this.$store.state.character.identify).name;
            if(this.$store.state.character){
                return this.$store.state.character.identify.name;
            }
            else
                return 'ISIMSIZ'
        }
    }
}
</script>