<template>
    <div class="chat" @keydown.esc="closeChatInput">
        <div class="messages q-px-md q-pt-sm font-shadow">
            <div class="text font-primary font-normal" v-for="msg in messages" :key="msg">
               <span v-html="msg"></span>
            </div>
        </div>
        <div class="input bg-primary font-normal font-primary" v-if="inputOpen">
            <input type="text" v-model="message" class="chatInput" ref="inputChat" @keydown.enter="sendMessage">
        </div>
    </div>
</template>

<style lang="scss" scoped>
.chat {
    position: fixed;
    top: 0;
    left: 0;
    width: 40%;    
    height: 30%;    
    
    .messages {
        height: 90%;
        max-height: 90%;
        overflow: auto;
        background: rgba(0, 0, 0, 0.124);
        font-size: calc(100% + 2px);
    }::-webkit-scrollbar-track
    {
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
        background-color: #988c9700;
    }::-webkit-scrollbar
    {
        width: 4px;
        background-color: #988c9700;
    }::-webkit-scrollbar-thumb
    {
        background-color: #4b4e678a;
        border: 1px solid #4b4e6729;
        border-radius: 25px;
        
    }
    
    .input {
        height: 10%;        
        .chatInput {
            height: 100%;
            width: 100%;            
            padding-left: 1%;
            background-color: rgba(0, 0, 0, 0.583);
        }:focus {
            outline: none;
        }
    }
}
</style>

<script>
export default {
    data: () => ({
        inputOpen: false,
        messages: [],
        message: '',
    }),

    methods: {
        openChatInput(){
            if(!this.inputOpen){
                this.inputOpen = true;
                //this.$refs.inputChat.focus()
                this.$nextTick(() => {
                    this.message=''
                    this.$refs.inputChat.focus()
                  
                })
            }
        },

        closeChatInput(){
            if(this.inputOpen){
                this.inputOpen = false;
                this.message=''
                if('alt' in window)
                    alt.emit('Gui:Chat:CloseChat')
            }
        },

        sendMessage() {
            if('alt' in window){
                alt.emit('Gui:Chat:Execute', this.message);
                this.message = ''
                this.closeChatInput();
            }            
        },

        messageHandler(message, type, side) {            
            /*if(this.messages.length > 5){
                this.messages = this.message.slice(1, this.messages.length)[1];
            }*/
            switch(type) {
                case 0: this.messages.push(this.colorify(message)); break;
                case 1: 
                    this.notify({
                        progress: true,
                        message: message,
                        icon: "error",
                        color: "red",
                        textColor: "white",
                        position: side,
                    }); break;
                case 2:
                    this.notify({
                        progress: true,
                        message: message,
                        icon: "warning",
                        color: "yellow",
                        textColor: "white",
                        position: side,
                    }); break;
                case 3:
                    this.notify({
                        progress: true,
                        message: message,
                        icon: "check",
                        color: "green",
                        textColor: "white",
                        position: side,
                    }); break;
                case 4:
                    this.notify({
                        progress: true,
                        message: message,
                        icon: "info",
                        color: "dark",
                        textColor: "primary",
                        position: side,
                    }); break;
            }
        },

        colorify(text) {
            let matches = [];
            let m = null;
                let curPos = 0;
                
            do {
                    m = /\{[A-Fa-f0-9]{3}\}|\{[A-Fa-f0-9]{6}\}/g.exec(text.substr(curPos));
                    
                if(!m) break;
                    
                matches.push({
                found: m[0],
                index: m['index'] + curPos
                    });
                    
                curPos = curPos + m['index'] + m[0].length;
                } 
            while(m != null);
                
                if (matches.length > 0) {
                        text += '</font>';
                        
                    for (let i = matches.length - 1; i >= 0; --i) {
                    let color = matches[i].found.substring(1, matches[i].found.length - 1);
                    let insertHtml = (i != 0 ? '</font>' : '') + '<font color="#' + color + '">';
                    text = text.slice(0, matches[i].index) + insertHtml + text.slice(matches[i].index + matches[i].found.length, text.length);
                    }
                }
                
        return text;
        },

        notify(data) {
            this.$parent.q.notify({
                progress: data.progress,
                message: data.message,
                icon: data.icon,
                color: data.color,
                textColor: data.textColor,
                position: data.position
            })
        }
    },

    mounted() {
        if('alt' in window){
            alt.on('Gui:Chat:OpenChat', this.openChatInput)
            alt.on('Gui:Chat:Server:Message', this.messageHandler)
        }
    }
}
</script>