<template>
    <div class="gui fullscreen non-selectable">
        <adminMenu v-if="this.$store.state.account.accountType > 4"/>

        <characterInfo />
        
        <streetView />

        <chatComp />

        <interaction v-if="interaction.state" v-bind:is="interaction"/>

        <businessInfo v-if="business.Id > 0"/>

        <!-- Dynamic Pages -->        
        <component v-bind:is="dynamicPage.staticPage[dynamicPage.dynamicPageSelection]" v-bind:data="dynamicPage.data"/>


    </div>
</template>

<script>
//import {shallowRef} from 'vue'
import { useQuasar } from 'quasar'
import { shallowRef } from 'vue'

import streetView from '@/components/Gui/streetView.vue'
import chatComp from '@/components/Gui/chat.vue'

//  character
import characterInfo from '@/components/Gui/character/characterInfo.vue'

// Business
import businessInfo from '@/components/Gui/business/entranceInformation.vue'
// Admin
import adminMenu from '@/components/Gui/admin/adminMenu.vue'

// interaction
import interaction from '@/components/Gui/interaction.vue'

// dynamic pages import
import editRentaCar from '@/components/Gui/admin/editRentaCar.vue'
import rentaCar from '@/components/Gui/utils/rentACar.vue'
import businessEdit from '@/components/Gui/business/editBusiness.vue'

const staticPages = shallowRef([
    // Car Rent ->
    editRentaCar, rentaCar,
    // ------
    businessEdit
])

export default {
    setup() {
        const q = useQuasar()
        return { q }        
    },

    data: () => ({        
        business: {
            showInfo: false,
            Id: -1
        },
        interaction: {
            state: false,
            text: '',
            data: '',
        },
        dynamicPage: {
            staticPage: staticPages,
            dynamicPageSelection: -1,
            data: []
        }
    }),

    components: {
        adminMenu,
        characterInfo,
        streetView,
        chatComp,
        // Business
        businessInfo,
        businessEdit,
        //Interaction 
        interaction
    },

    mounted() {
        if('alt' in window){
            alt.on('GUI:Business:ShowInfo', this.businessShowInfo)
            alt.on('GUI:Business:CloseInfo', this.businessCloseInfo)

            //interaction
            alt.on('Streamer:Interaction:Show', this.interactionShow)
            alt.on('Streamer:Interaction:Hide', this.interactionHide)

            alt.on('Gui:DynamicPages:Set', this.dynamicPageSelect)
            alt.on('Gui:DynamicPages:Unset', this.dynamicPageClose)

            alt.on('Gui:Dialog:Send', this.showDialog);
        }
    },

    methods: {
        

        businessShowInfo(id) {
            this.business.showInfo = true;
            this.business.Id = id
        },
        businessCloseInfo() {
                this.business.showInfo = false            
        },

        interactionShow(text, data) {
            if(data) this.interaction.data = data;
            console.log(text, data);
            this.interaction.text = text;
            setTimeout(() => {
                this.interaction.state = true;
            }, 200);
        },
        interactionHide() {
            this.interaction.text = '';
            this.interaction.data = undefined;
            this.interaction.state = false;
        },
        
        dynamicPageSelect(type, data) {
            if(data)
                this.dynamicPage.data = data;
            else
                this.dynamicPage.data = null;
            switch(type) {
                case 'editRentaCar': this.dynamicPage.dynamicPageSelection = 0; break;
                case 'rentACar': this.dynamicPage.dynamicPageSelection = 1; break;

                default:
                    this.dynamicPage.dynamicPageSelection = -1;
                    break;
            }
        },
        dynamicPageClose(){
            this.dynamicPageSelection = -1;
        },

        showDialog(data) {
            switch(data.type) {
                // alt.emit('Gui:Dialog:CallBack', true, data.callBack, data.callBackData);
                // alt.emit('Gui:Dialog:CallBack', false, data.callBack, data.callBackData);
                case 1:
                    this.q.dialog({
                        dark: true,
                        title: data.title,
                        message: data.message,
                        persistent: data.options.persistent ?? false,
                        ok: data.options.buttons.ok ?? undefined,
                        cancel: data.options.buttons.cancel ?? true
                    }).onOk(() => {
                        alt.emit('Gui:Dialog:CallBack', true, data.callBack, data.callBackData);
                    })
                    // }).onOk(() => {
                    //     alt.emit('Gui:Dialog:CallBack', true, data.callBack, data.callBackData)
                    // }).onCancel(() => {
                    .onCancel(() => {
                        alt.emit('Gui:Dialog:CallBack', false, data.callBack, data.callBackData)
                    })
                    // .onDismiss(() => {
                    //     alt.emit('Gui:Dialog:CallBack', false, data.callBack, data.callBackData);
                    // })
                break;

                case 2: 
                    this.q.dialog({
                        dark: true,
                        title: data.title,
                        message: data.message,
                        ok: data.options.buttons.ok ?? undefined,
                        cancel: data.options.buttons.cancel ?? true
                    }).onOk(() => {
                        alt.emit('Gui:Dialog:CallBack', true, data.callBack, data.callBackData);
                    }).onCancel(() => {
                        alt.emit('Gui:Dialog:CallBack', false, data.callBack, data.callBackData);
                    })
                    // .onDismiss(() => {
                    //     alt.emit('Gui:Dialog:CallBack', false, data.callBack, data.callBackData);
                    // })
                    break;

                case 3:
                    this.q.dialog({
                        dark: true,
                        title: data.title,
                        message: data.message,
                        prompt: data.options.prompt ?? {
                            model: '',
                            type: data.inputOptions.type // optional
                        },
                        persistent: data.options.persistent ?? false,
                        ok: data.options.buttons.ok ?? undefined,
                        cancel: data.options.buttons.cancel ?? true
                    }).onOk(res => {
                        data.callBackData.input = res;
                        alt.emit('Gui:Dialog:CallBack', true, data.callBack, data.callBackData);
                    }).onCancel(() => {
                        alt.emit('Gui:Dialog:CallBack', false, data.callBack, data.callBackData);
                    })
                    // .onDismiss(() => {
                    //     alt.emit('Gui:Dialog:CallBack', false, data.callBack, data.callBackData);
                    // })
                    break;

                case 4:
                    this.q.dialog({
                        title: data.title,
                        message: data.message,
                        options: data.options.options ?? {
                            type: 'radio',
                            model: 'opt1',
                            // inline: true
                            items: [
                                { label: 'Option 1', value: 'opt1', color: 'secondary' },
                                { label: 'Option 2', value: 'opt2' },
                                { label: 'Option 3', value: 'opt3' }
                            ]
                        },
                        ok: data.options.buttons.ok ?? undefined,
                        cancel: data.options.buttons.cancel ?? true,
                        persistent: data.options.persistent ?? false,
                    }).onOk(res => {
                        data.callBackData.input = res;
                        alt.emit('Gui:Dialog:CallBack', true, data.callBack, data.callBackData);
                    }).onCancel(() => {
                        alt.emit('Gui:Dialog:CallBack', false, data.callBack, data.callBackData);
                    })
                    // .onDismiss(() => {
                    //     alt.emit('Gui:Dialog:CallBack', false, data.callBack, data.callBackData);
                    // })
                    break;

                case 5:
                    this.q.dialog({
                        title: data.title,
                        message: data.message,
                        options: data.options.options ?? {
                            type: 'checkbox',
                            model: [],
                            // inline: true
                            items: [
                                { label: 'Option 1', value: 'opt1', color: 'secondary' },
                                { label: 'Option 2', value: 'opt2' },
                                { label: 'Option 3', value: 'opt3' }
                            ]
                        },
                        ok: data.options.buttons.ok ?? undefined,
                        cancel: data.options.buttons.cancel ?? true,
                        persistent: data.options.persistent ?? false,
                    }).onOk(res => {
                        data.callBackData.input = res;
                        alt.emit('Gui:Dialog:CallBack', true, data.callBack, data.callBackData);
                    }).onCancel(() => {
                        alt.emit('Gui:Dialog:CallBack', false, data.callBack, data.callBackData);
                    })
                    // .onDismiss(() => {
                    //     alt.emit('Gui:Dialog:CallBack', false, data.callBack, data.callBackData);
                    // })
                    break;

                case 6:
                    this.q.dialog({
                        title: data.title,
                        message: data.message,
                        options: data.options.options ?? {
                        type: 'toggle',
                        model: [],
                        // inline: true,
                        items: [
                            { label: 'Option 1', value: 'opt1', color: 'secondary' },
                            { label: 'Option 2', value: 'opt2' },
                            { label: 'Option 3', value: 'opt3' }
                        ]
                        },
                        ok: data.options.buttons.ok ?? undefined,
                        cancel: data.options.buttons.cancel ?? true,
                        persistent: data.options.persistent ?? false,
                    }).onOk(res => {
                        data.callBackData.input = res;
                        alt.emit('Gui:Dialog:CallBack', true, data.callBack, data.callBackData);
                    }).onCancel(() => {
                        alt.emit('Gui:Dialog:CallBack', false, data.callBack, data.callBackData);
                    })
                    // .onDismiss(() => {
                    //     alt.emit('Gui:Dialog:CallBack', false, data.callBack, data.callBackData);
                    // })
            }
        }
    },

    computed: {
        dynamicPageSelection() {
            console.log(staticPages[this.dynamicPage.dynamicPageSelection])
            return staticPages[this.dynamicPage.dynamicPageSelection];
        }
    }
}
</script>

<style lang="scss" scoped>
.hiddenMenu {
    display: none;
}
</style>