<template>
    <div class="interaction slide-in-blurred-right absolute inset-y-1/4 right-0 w-1/6" v-if="interaction">
        <div v-if="interaction.data && interaction.data.type == 'business'">
            <q-img :src="interaction.data.image" height="200px" fit="fill">
                <div class="text-h4 text-center font-bold font-accent bg-transparent absolute-full flex flex-center font-shadow">{{interaction.data.name}}</div>
            </q-img>

            <div class="text-h6 font-normal font-primary text-center">{{interaction.data.price}}</div>
        </div>

        <div class="interact object-center bg-gradient-to-l from-purple-800 font-normal font-primary font-shadow h-10 q-pt-sm" v-html="interaction.text"></div>
    </div> 
</template>

<script>
export default {
    data: () => ({
        interaction: {}
    }),
    methods: {
        getInteractionText() {
            return this.interaction.text.replace('[<', '<div class="keyboard">').replace('>]', '</div>');
            //return 'SELAM'
        },

        getDataText() {
            let data = this.$parent.interaction.data;
            let text;
            if(data.image){
                text += "<q-img src='" + data.image + "' />";
            }

            if(data.name)
                text += "<div class='text-h6 text-center font-normal font-primary'>" + data.name + "</div>";

            if(data.owner)
                text += "<div class='text font-normal font-primary'>" + data.owner + "</div>";

            return text;
        }
    },
    
    mounted() {
        this.interaction = this.$parent.interaction;
    }
}
</script>